import React from 'react';
import { Row, Input } from 'react-materialize';

class Select extends React.Component {
	render() {
		const lists = this.props.lists;
		return (
			<Row>
				<Input s={12} type="select" name="bestseller-list-select" id="bestseller-list-select" onChange={this.props.onChange}>
					{lists.map((list, key) => {
						return <option key={key} className="bestseller-list-select-item">{list.list_name}</option>
					})}
				</Input>
			</Row>
		);
	}
}

Select.propTypes = {
	lists: React.PropTypes.array.isRequired,
	onChange: React.PropTypes.func.isRequired,
}

export default Select;


