import React from 'react';
import 'whatwg-fetch';
import { Row, Col } from 'react-materialize';

import Header from './Header';
import BestsellerList from './BestsellerList';
import Select from './Select';

const apiUrl = 'https://api.nytimes.com/svc/books/v3/';
const apiKey = '0242de3cd44e4542a9f1a51b0eeef4d3';

class BestsellerWidget extends React.Component {
	constructor() {
		super();
		this.getBestsellerList = this.getBestsellerList.bind(this);
		this.getListIndex = this.getListIndex.bind(this);
		this.handleChange = this.handleChange.bind(this);

		this.state = {
			loading: true,
			data: {},
			selectedList: ''
		}
	}

	componentDidMount() {
		this.getBestsellerList('lists/overview');
	}

	getBestsellerList(list) {
		const fullUrl = `${apiUrl + list}.json?api-key=${apiKey}`;
		fetch(fullUrl)
			.then((response) => {
				return response.json()
			}).then((json) => {
				// console.log('parsed json', json)
				this.setState({
					data: json
				})
			}).then(() => {
				this.setState({
					selectedList: document.querySelector('#bestseller-list-select').value,
					loading: false
				})
			})
	}

	getListIndex() {
		const results = this.state.data.results;
		if (results) {
			let index;
			const lists = results.lists;
			for (var i = 0; i < lists.length; i++) {
				if (lists[i].hasOwnProperty('list_name') && lists[i]['list_name'] === this.state.selectedList) {
					index = i;
				}
			}
			return index;
		}
	}

	handleChange(event) {
		this.setState({
			selectedList: event.target.value
		})
	}

	render() {
		const config = this.props.config;
		const data = this.state.data;
		const index = data.results ? this.getListIndex() : 0;
		// console.log("rendering", data.results);

		return (
			<Row>
				<Col s={12}>
					<Header text={config.headerText ? config.headerText : 'Bestsellers'} />
					<Select lists={data.results ? data.results.lists : []} onChange={this.handleChange} />
					{this.state.loading ?
						<div>Loading...</div>
						:
						<BestsellerList list={this.state.selectedList} listItems={data.results.lists[index]} searchUrl={config.libraryCatalogSearchUrl ? config.libraryCatalogSearchUrl : ''} />
					}
				</Col>
			</Row>
		);
	}
}

BestsellerWidget.propTypes = {
	config: React.PropTypes.object.isRequired,
}

export default BestsellerWidget;


