import React from 'react';
import BestsellerListItem from './BestsellerListItem';

class BestsellerList extends React.Component {
	constructor() {
		super();
		this.onClick = this.onClick.bind(this);
	}

	onClick(event) {
		const isbn = event.currentTarget.dataset.isbn;
		// const amazonSearchUrl = `https://www.amazon.com/gp/search/ref=sr_adv_b/?field-isbn=${isbn}`;
		// const libraryCatalogSearchUrl = `http://catalog.bburglibrary.net/POLARIS/search/searchresults.aspx?ctx=3.1033.0.0.2&type=Keyword&term=${isbn}&by=ISBN`;
		if (this.props.searchUrl) {
			// window.open(amazonSearchUrl, '_blank');
			window.open(this.props.searchUrl + isbn, '_blank');
		}
	}

	render() {
		// const lists = this.props.lists;
		// const bookIndices = Object.keys(this.props.listItems.books);
		// console.log("from list", this.props.listItems);
		const isClickable = this.props.searchUrl ? true : false;

		return (
			<ol className="bookList">
				{this.props.listItems.books.map((book, key) => {
					return <BestsellerListItem key={key} bookData={book} onClick={this.onClick} isClickable={isClickable}/>
				})}
			</ol>
		);
	}
}

BestsellerList.propTypes = {
	list: React.PropTypes.string.isRequired,
	listItems: React.PropTypes.object,
	searchUrl: React.PropTypes.string.isRequired,
}

export default BestsellerList;


