import React from 'react';

function BestsellerListItem(props) {
	const book = props.bookData;

	return (
		<li className="card" data-isbn={book.primary_isbn13} onClick={props.onClick} style={props.isClickable ? {cursor: "pointer"} : {}}>
			<div className="card-content">
				<div>
					<div className="card-title book-title">{book.title}</div>
					<p className="book-author">{book.author}</p>
				</div>
				<p className="book-description">{book.description}</p>
			</div>
		</li>
	);
}

BestsellerListItem.propTypes = {
	bookData: React.PropTypes.object.isRequired,
	onClick: React.PropTypes.func.isRequired,
	isClickable: React.PropTypes.bool.isRequired,
}

export default BestsellerListItem;


